import { API } from 'api';
import { ApiError, AuthError, ServerError } from 'api/errors';
import { useErrors } from 'hooks';
import { useAuth } from 'hooks/useAuth';
import { useQuery } from 'react-query';
import { mapSocialWorkFromAPI } from 'utils/entities/socialWork';
import { getLibraryWordKey } from './../keys';

export const useLibraryWord = (id: string) => {
  const { addError } = useErrors();
  const { handleLogout } = useAuth();

  const query = useQuery(
    getLibraryWordKey(id),
    // is number
    () => API.admin.library.get(id as any),
    {
      onError: (e) => {
        if (e instanceof ServerError) {
          addError('Произошла критическая ошибка при загрузке термина!');
        } else if (e instanceof AuthError) {
          handleLogout();
        } else if (e instanceof ApiError) {
          addError(e.message);
        }
      },
    }
  );

  return {
    ...query,
    apiError: query.data?.error,
    apiData: query.data?.data ?? null,
    isError: query.isError || (query.data?.error ? true : false),
  };
};
