kground);\n    border-radius: 6px;\n  }\n\n  &__columnHeader {    \n    padding: 20px 10px;\n\n    &_sorting {\n      user-select: none;\n      cursor: pointer;\n    }\n\n    &_label {\n      color: var(--gray);\n    }\n\n    &_main {  \n      flex-basis: 20%;\n      flex-grow: 0;\n    }\n\n    &_secondary {\n      flex-basis: 8%;\n      flex-grow: 0;\n      text-align: center;\n    }\n  }\n\n  &__content {\n    margin: 15px 0;\n    width: 100%;\n    height: 100%;\n    display: flex;\n    flex-wrap: wrap;\n  }\n\n  &__row {\n    padding: 0 20px;\n    width: 100%;\n    display: flex;\n    gap: 0px;\n    align-items: center;\n    border-radius: 6px;\n    cursor: pointer;\n    transition: .2s;\n\n    &:hover {\n      background-color: var(--background);\n      // box-shadow: 0px 5px 60px rgba(140, 145, 181, 0.15);\n      transition: .2s;\n    }\n\n    &_main {  \n      padding: 25px 10px;\n      flex-basis: 20%;\n      flex-grow: 0;\n      border-top: 0px solid var(--gray);\n      border-bottom: 0px solid var(--gray);\n    }\n\n    &_secondary {\n      height: 100%;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      padding: 25px 10px;\n      flex-basis: 8%;\n      flex-grow: 0;\n      text-align: center;\n      word-break: break-word;\n      border-left: 1px solid var(--gray);\n    }\n\n    &_total {\n      margin-top: 15px;\n      border: 1px solid var(--blue);\n      box-shadow: 0px 5px 60px rgba(140, 145, 181, 0.15);\n      \n      & * {\n        font-weight: bold;\n      }\n\n      & .table__row_secondary {\n        flex-basis: 8%;\n        border-left: 0px solid var(--gray);\n      }\n    }\n  }\n\n  &__footer {\n    width: 100%;\n    padding: 30px;\n    display: flex;\n    gap: 20px;\n    align-items: center;\n    justify-content: space-between;\n    background: var(--background);\n    border-radius: 6px;\n\n    &Info *{\n      color: var(--gray);\n    }\n\n    &Pagination {\n      display: flex;\n      align-items: center;\n      gap: 20px;\n\n      &Controller {\n        display: flex;\n        align-items: center;\n        gap: 4px;\n      }\n\n      & * {\n        color: var(--gray);\n      }\n    }\n\n    &Control {\n      width: 8px;\n      height: 16px;\n      cursor: pointer;\n    }\n  }\n\n  &__paginator {\n    border: none;\n    background: none;\n    width: 70px;\n    height: 60px;\n    text-align: center;\n    border: 1px solid rgba(140, 145, 181, 0.5);\n    border-radius: 8px;\n    transition: .2s;\n\n    font-family: \"Commissioner Regular\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 14px;\n    line-height: 17px;\n\n    &:focus {\n      border-color: var(--blue);\n      transition: .2s;\n    }\n\n    &::-webkit-outer-spin-button,\n    &::-webkit-inner-spin-button {\n      -webkit-appearance: none;\n      margin: 0;\n    }\n    -moz-appearance: textfield;\n\n    &Heading {\n      color: var(--black);\n    }\n  }\n\n  &__loader {\n    width: 100%;\n    min-height: calc(70vh - 155px);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n  }\n}"],"sourceRoot":""}]);
